body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,body{
  background-color: #282C34;
}
.App {
  padding-top: 70px;
  padding-bottom: 70px;
  background-color: #282C34;
}

.img-responsive{
  display: block;
  max-width: 100%;
  height: auto;
  margin-left: auto;
  margin-right: auto;
}

.message-list{
  color:white;
  border-left: 1px #353A3F solid;
  height: 80vh;
  overflow-y: scroll;
}

.message-dividers{
  border-top: 1px #353A3F solid;
}

.message-image{
  display: block;
  min-width: 32px;
  max-width: 100%;
  height: auto;
  margin-left: auto;
  margin-right: auto;
}
